/* jsencryptRSA */
// import JSEncrypt from 'jsencrypt' //  jsencrypt.js Message too long for RSA
/* encryptlongRSA */


import Encrypt from './jsencrypt.js' // encryptlongjsencrypt

import {SobRsaConfig,SobRsaClass} from  './index.d'

export class SobRSA implements SobRsaClass {
    whiteList:Array<any>
    publicKey:String
    privateKey:String
    isDebugger:Boolean
    isOpen:Boolean
    constructor(config:SobRsaConfig){
        if(!config){
            throw Error('required init options!')
        }
        this.isOpen = config.isOpen
        this.whiteList = config.whiteList
        this.publicKey = config.publicKey
        this.privateKey = config.privateKey
        this.isDebugger = config.isDebugger
    }
    /**
 * @description: 
 * @param {*} path 
 * @return {*} 01 
 */
    filterWhiteList(path:String) {
        const serviceApi = this.whiteList
        if (
            !this.isOpen ||
            (serviceApi && serviceApi.findIndex((e:any) => path.includes(e)) !== -1)
        ) {
            return 0
        }
        return 1
    }

    /**
     * @description: RSA
     * @param {*} data 
     * @param {*} publicKey RSA
     * @return {*}
     */
    encode(data:any, publicKey:String = this.publicKey):String {
        var encryptor = new Encrypt()
        encryptor.setPublicKey(publicKey)
        return encryptor.encryptLong(data)
    }
    /**
     * @description: RSA
     * @param {*} data 
     * @param {*} privateKey RSA
     * @return {*}
     */
    decode(data:any, privateKey:String = this.privateKey) {
        var encryptor = new Encrypt()
        encryptor.setPrivateKey(privateKey)
        return encryptor.decryptLong(data)
    }
    /**
     * @description: 
     * @param {*} params 
     * @param {*} path 
     * @return {*}
     */
    encrypt  (params:any, path:String)  {
        if (JSON.stringify(params) === '{}') return {}
        const isPass = this.filterWhiteList(path)
        if (!isPass) {
            return params
        }
        const result = this.encode(encodeURIComponent(JSON.stringify(params)))
        if (this.isDebugger) {
            console.group()
            console.log(':' + path)
            console.log(': ' + JSON.stringify(params))
            console.log('URI: ' + encodeURIComponent(JSON.stringify(params)))
            console.log('RSA: ' + result)
            console.groupEnd()
        }
       
        return {
            encryptData: result
        }
    }

    /**
     * @description: 
     * @param {*} params 
     * @param {*} path 
     * @return {*}
     */
    decrypt  (params:any, path:String)  {
        if (JSON.stringify(params) === '{}') return {}
        const isPass = this.filterWhiteList(path)
        if (!isPass) {
            return params
        }
        const result = this.decode(params)
        if(this.isDebugger){
            console.group()
            console.log(':' + path)
            console.log(': ' + JSON.stringify(params))
            console.log('RSA: ' + result)
            console.log('URI: ' + JSON.stringify(JSON.parse(decodeURIComponent(result.replace(/\+/g, '%20')))))
            console.groupEnd()
        }
       
        return JSON.parse(decodeURIComponent(result.replace(/\+/g, '%20')))
    }


}


